.means {
  padding-bottom: 160px;
  @media screen and (max-width: 768px) {
    padding-bottom: 50px;
  }
}

.means__title {
  text-align: center;
}

.means__text {
  text-align: center;
  max-width: 895px;
  margin-bottom: 48px;
}

.cards {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  .card {
    max-width: 427px;
    padding: 64px 24px;
    background-color: $white;
    border-radius: 8px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 50px;
    .figure__circle {
      @extend %circle_figure;
      margin: 0 auto 24px auto;
    }
    &__title {
      @extend %means_card_title;
      margin-bottom: 16px;
    }
    &__text {
      @extend %means_card_text;
    }
    @media screen and (max-width: 1024px) {
      margin-bottom: 50px;
    }
    @media screen and (max-width: 768px) {
      margin-bottom: 50px;
      max-width: 220px;
      padding: 32px 12px;
    }
  }
  @media screen and (max-width: 1024px) {
    justify-content: space-around;
  }
  @media screen and (max-width: 768px) {
    justify-content: space-around;
    flex-wrap: wrap;
  }
}
