.circles {
  &::before {
    content: "";
    display: block;
    width: 64px;
    height: 64px;
    background-color: $black;
    opacity: 0.8;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: -50%;
    transform: translate(50%, -50%);
  }
  &::after {
    content: "";
    display: block;
    width: 64px;
    height: 64px;
    background-color: $green;
    opacity: 0.8;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    right: -50%;
    transform: translate(-50%, -50%);
  }
}

.triangle {
  width: 0;
  height: 0;
  border-left: 28px solid transparent;
  border-right: 28px solid transparent;
  border-bottom: 48px solid $green;
  opacity: 0.8;
  &::before {
    content: "";
    display: block;
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-bottom: 24px solid $black;
    transform: translateY(100%);
  }
  &::after {
    content: "";
    display: block;
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-bottom: 24px solid $black;
    transform: translate(-100%, 0);
  }
}

.star {
  width: 56px;
  height: 56px;
  background-color: $green;
  border-radius: 50%;
  position: relative;
  opacity: 0.8;
  display: flex;
  justify-content: center;
  align-items: center;
  &::before {
    content: "";
    display: block;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: $black;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .pe-7s-star {
    color: $light_gold;
    position: relative;
    z-index: 10;
  }
}

.kickstart__circle {
  z-index: 1;
  .arrow {
    border-left: 36px solid transparent;
    border-right: 36px solid transparent;
    border-bottom: 72px solid $green;
    transform: rotate(90deg) translateY(30%);
    position: relative;
    &::before {
      content: "";
      display: block;
      border-left: 22px solid transparent;
      border-right: 22px solid transparent;
      border-bottom: 40px solid #81bfa8;
      transform: translate(-50%, -20%);
      position: absolute;
    }
  }
  .target {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    background-color: $gray;
    position: absolute;
    transform: translateX(40%);
    z-index: -1;
    &::before {
      content: "";
      display: block;
      width: 24px;
      height: 24px;
      border-radius: 50%;
      background-color: #807e76;
      position: relative;
      margin: 0 auto;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.onboard__circle {
  z-index: -1;
  .onboard_max_circle {
    width: 64px;
    height: 64px;
    background-color: $green;
    opacity: 0.2;
    border-radius: 50%;
    position: relative;
  }
  .onboard_premax_circle {
    width: 48px;
    height: 48px;
    background-color: $green;
    opacity: 0.4;
    border-radius: 50%;
    position: absolute;
  }
  .onboard_mid_circle {
    width: 34px;
    height: 34px;
    background-color: $green;
    opacity: 0.6;
    border-radius: 50%;
    position: absolute;
  }
  .onboard_small_circle {
    width: 18px;
    height: 18px;
    background-color: $green;
    opacity: 0.6;
    border-radius: 50%;
    position: absolute;
  }
}

.engage__circle {
  &__black {
    width: 45px;
    height: 45px;
    background-color: $black;
    border-radius: 50%;
  }
  &__half {
    width: 64px;
    height: 64px;
    background-color: $green;
    opacity: 0.5;
    position: absolute;
    clip-path: circle(50% at center right);
    transform: translate(-35%, 35%) rotate(-45deg);
  }
}
