.illustration_bot {
  padding: 44px 0;
  display: flex;
  align-items: center;
  &__title {
    max-width: 644px;
  }
  &__text {
    max-width: 644px;
    margin-bottom: 40px;
  }
  &__button {
    @extend %button;
  }
  &__img {
    max-width: 100%;
    max-height: 100%;
    @media screen and (max-width: 1024px) {
      max-width: 50%;
      max-height: 50%;
    }
    @media screen and (max-width: 768px) {
      display: none;
    }
  }
  @media screen and (max-width: 768px) {
    justify-content: center;
  }
}
