//ProximaNova

@font-face {
  src: url(./fonts/ProximaNova-Regular.ttf);
  font-family: "ProximaNova";
  font-weight: 400;
}

@font-face {
  src: url(./fonts/ProximaNova-Bold.ttf);
  font-family: "ProximaNova";
  font-weight: 700;
}

$proxima: "ProximaNova";

//Futura

@font-face {
  src: url(./fonts/FuturaRoundDemi.ttf);
  font-family: "FuturaRoundDemi";
  font-weight: 500;
}

$futura: "FuturaRoundDemi";

//BelyDisplay

@font-face {
  src: url(./fonts/BelyDisplay.ttf);
  font-family: "BelyDisplay";
  font-weight: 400;
}

$bely: "BelyDisplay";

//font-family: 'Source Code Pro', monospace;

@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@500;600;700&display=swap");

$source: "Source Code Pro", monospace;

//icon

@font-face {
  font-family: "Pe-icon-7-stroke";
  src: url("./fonts/Pe-icon-7-stroke.ttf");
  font-weight: normal;
  font-style: normal;
}

[class^="pe-7s-"],
[class*=" pe-7s-"] {
  display: inline-block;
  font-family: "Pe-icon-7-stroke";
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.pe-7s-star:before {
  content: "\e611";
}
