.illustration__top {
  position: relative;
  display: flex;
  align-items: center;
  padding: 40px 0 200px 0;
  @media screen and (max-width: 768px) {
    justify-content: center;
    text-align: center;
    padding-bottom: 40px;
    font-size: 0.8em;
  }
}

.illustration__top__block {
  max-width: 630px;
  display: inline-block;
  &__title {
    @extend %illustration_title;
    font-size: 4em; // 64px
    line-height: 1.25em; // 80px
  }
  &__text {
    @extend %illustration_text;
    font-size: 1.5em; // 24px
    line-height: 1.333333333333333em; // 32px
    margin-top: 24px;
  }
  &__button {
    @extend %button;
    margin-top: 40px;
  }
}

.top__img__container {
  width: 670px;
  height: 720px;
  position: relative;
  background-color: $light_gold;
  border-radius: 200px 300px 350px 250px;
  display: flex;
  align-items: center;
  .top__img {
    max-width: 100%;
    max-height: 100%;
    @media screen and (max-width: 1024px) {
      max-width: 70%;
      max-height: 70%;
      position: absolute;
      right: 0;
      bottom: 50px;
    }
    @media screen and (max-width: 768px) {
      display: none;
    }
  }
  @media screen and (max-width: 1024px) {
    position: absolute;
    z-index: -1;
    top: 5%;
    right: 0;
    max-width: 100%;
    max-height: 100%;
  }
}
