.header {
  padding: 40px 0;
}

.header__logo,
.logo {
  font-family: $futura;
  font-size: 2em; // 32px
  display: flex;
  justify-content: space-between;
  align-items: center;
  .logo__text {
    display: inline-block;
    width: 250px;
    margin-right: 50px;
    @media screen and (max-width: 1024px) {
      display: none;
      margin-right: 50px;
    }
    @media screen and (max-width: 768px) {
      display: unset;
      margin-right: unset;
      width: 160px;
      font-size: 0.8em;
    }
  }
  .ce {
    position: relative;
    display: inline-block;
    width: 70px;
    .e {
      position: absolute;
      left: 27px;
    }
    @media screen and (max-width: 1024px) {
      margin-right: 30px;
    }
    @media screen and (max-width: 768px) {
      margin-right: unset;
    }
  }
}

.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  &__link {
    font-size: 1.25em; //20px
    font-family: $proxima;
    font-weight: 400;
    padding: 2px;
    &:not(:last-child) {
      @media screen and (max-width: 1024px) {
        margin-right: 10px;
      }
      @media screen and (max-width: 768px) {
        margin-right: unset;
      }
    }
    &:hover {
      color: $green;
      @media screen and (max-width: 1024px) {
        color: $white;
        background-color: $green;
        padding: 2px;
        border-radius: 8px;
      }
    }
    @media screen and (max-width: 768px) {
      background-color: $green;
      border-radius: 8px;
      margin: 5px;
    }
  }
  @media screen and (max-width: 768px) {
    display: none;
    position: absolute;
    bottom: 0;
    right: 0;
    transform: translateY(100%);
  }
}

//HAMBURGER

.checkbox {
  display: none;
  &:checked ~ .nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    z-index: 10;
    padding: 5px;
    width: auto;
    border: 4px solid #303031;
    background-color: $white;
    border-radius: 16px;
    font-size: 2em;
  }
}

.burger {
  margin: 0 auto;
  width: 30px;
  height: 30px;
  position: relative;
  display: none;
  .bar {
    padding: 0;
    width: 30px;
    height: 4px;
    background-color: $green;
    display: block;
    border-radius: 4px;
    transition: all 0.4s ease-in-out;
    position: absolute;
  }
  @media screen and (max-width: 768px) {
    display: block;
  }
}

.bar1 {
  top: 0;
}

.bar2,
.bar3 {
  top: 13.5px;
}

.bar3 {
  right: 0;
}

.bar4 {
  bottom: 0;
}

.checkbox:checked + label > .burger > .bar1 {
  transform: rotate(45deg);
  transform-origin: 5%;
  width: 41px;
}

.checkbox:checked + label > .burger > .bar2 {
  transform: translateX(-40px);
  background-color: transparent;
}

.checkbox:checked + label > .burger > .bar3 {
  transform: translateX(40px);
  background-color: transparent;
}

.checkbox:checked + label > .burger > .bar4 {
  transform-origin: 5%;
  transform: rotate(-45deg);
  width: 41px;
}
