.engage {
  display: flex;
  justify-content: space-between;
  padding-bottom: 160px;
  @media screen and (max-width: 1439px) {
    flex-direction: column;
    align-items: center;
  }
}

.block {
  max-width: 644px;
  .engage__circle {
    @extend %circle_figure;
    margin-bottom: 24px;
  }
  @media screen and (max-width: 768px) {
    text-align: center;
  }
}

.widgets {
  max-width: 700px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  .write {
    @extend %widget;
    min-width: 560px;
    padding: 24px;
    box-shadow: 0 8px 0 $green;
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    .write__container {
      max-width: 404px;
      .write__title {
        display: block;
        font-weight: 700;
        margin-bottom: 16px;
        font-size: 1.125em; // 18px
        line-height: 1.333333333333333em; // 24px
      }
      .write__text {
        font-weight: 500;
        font-size: 1em; // 16px
        line-height: 1.5em; // 24px
      }
    }
    @media screen and (max-width: 768px) {
      min-width: 200px;
    }
  }
  .appli {
    @extend %widget;
    max-width: 381px;
    min-width: 381px;
    padding: 32px 40px;
    background-color: $light_gold;
    &__title {
      font-weight: 700;
      font-size: 1.5em; // 24px
      line-height: 1.333333333333333em; // 32px
      margin-bottom: 24px;
      display: block;
    }
    &__client {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .circle_and_name {
        display: flex;
        align-items: center;
        .circle__small__client {
          @extend %circle_small_client;
          margin-right: 16px;
        }
        .name {
          font-weight: 600;
          font-size: 1.25em; // 20px
          line-height: 1.2em; // 24px
        }
      }
      .rate {
        display: flex;
        align-items: center;
        .rate_star {
          color: $yellow;
          margin-right: 8px;
          font-size: 1.5em; // 24px
        }
        .rate__num {
          font-weight: 500;
          font-size: 1.25em; // 20px
          line-height: 1.2em; // 24px
        }
      }
      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }
    @media screen and (max-width: 768px) {
      min-width: 200px;
    }
  }
  .profile {
    @extend %widget;
    padding: 24px;
    min-width: 240px;
    max-width: 240px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 40px;
    margin-left: 12px;
    .circle__client {
      @extend %circle_client;
      margin-bottom: 16px;
    }
    &__name {
      font-weight: 700;
      font-size: 1.25em; // 20px
      line-height: 1.2em; // 24px
      margin-bottom: 4px;
    }
    &__prof {
      font-weight: 500;
      font-size: 1em; // 16px
      line-height: 1.5em; // 24px
      color: $gray;
      margin-bottom: 16px;
    }
    &__commits {
      display: flex;
      justify-content: space-between;
      margin-bottom: 16px;
      align-items: center;
      .commits__much {
        font-size: 1em; // 16px
        line-height: 1.5em; // 24px
        color: $black;
        margin-right: 40px;
      }
      .commits__rate {
        display: flex;
        align-items: center;
        .commits__rate__star {
          color: $yellow;
          font-size: 1.5em;
        }
      }
    }
    &__button {
      padding: 12px 30px;
      border: 0;
      border-radius: 16px;
      background-color: $black;
      color: $white;
      font-weight: 500;
      font-size: 1em; // 16px
      line-height: 1.25em; // 20px
      letter-spacing: -0.02em;
    }
  }
  @media screen and (max-width: 1439px) {
    margin-top: 30px;
  }
  @media screen and (max-width: 768px) {
    justify-content: center;
  }
}
