.onboard {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 144px;
  .widget {
    @extend %widget;
    box-shadow: 16px 16px 0 $green;
    margin-right: 30px;
    box-shadow: none;
    position: relative;
    &::before {
      content: "";
      display: block;
      width: 258px;
      height: 258px;
      border: 6px dotted $light_gold;
      border-top: 0;
      border-right: 0;
      position: absolute;
      left: -15px;
      bottom: -15px;
    }
    &::after {
      content: "";
      display: block;
      width: 270px;
      height: 270px;
      border: 6px dotted $light_gold;
      border-top: 0;
      border-right: 0;
      position: absolute;
      left: -27px;
      bottom: -27px;
    }
    &__text {
      max-width: 404px;
      font-size: 1em;
      line-height: 1.625em;
      margin-bottom: 24px;
    }
    .posttext {
      display: flex;
      max-width: 280px;
      justify-content: space-between;
      margin-bottom: 32px;
      .commit {
        text-transform: uppercase;
        display: flex;
        flex-direction: column;
        .postname {
          margin-bottom: 12px;
        }
      }
      .nums {
        text-transform: uppercase;
        margin-bottom: 12px;
        display: flex;
        flex-direction: column;
        .postname {
          margin-bottom: 12px;
        }
      }
    }
    .widget__button {
      max-width: 404px;
      padding: 12px 100px;
      border-radius: 16px;
      background-color: $black;
      color: $white;
      border: 0;
      font-size: 1.5em; // 24px
      line-height: 1.333333333333333em; // 32px
      @media screen and (max-width: 768px) {
        padding: 12px 80px;
        margin: 0 auto;
        display: block;
      }
    }
    @media screen and (max-width: 768px) {
      margin-right: unset;
    }
  }
  .block {
    max-width: 644px;
    .onboard__circle {
      @extend %circle_figure;
      margin-bottom: 24px;
    }
    @media screen and (max-width: 768px) {
      text-align: center;
    }
  }
  @media screen and (max-width: 768px) {
    flex-wrap: wrap;
    flex-direction: column-reverse;
    min-width: 200px;
  }
}
