.help {
  padding-bottom: 44px;
  &__title {
    text-align: center;
  }
  &__text {
    text-align: center;
    max-width: 895px;
    margin-bottom: 48px;
  }
  &__button {
    padding: 20px 24px;
    background-color: $black;
    border-radius: 8px;
    border: 0;
    margin: 0 auto;
    display: block;
    color: $white;
    font-family: $proxima;
    font-weight: 700;
    font-size: 1.125em; // 18px
    line-height: 1.333333333333333em; // 24px
  }
}

.notes {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
  margin-bottom: 48px;
  .note {
    max-width: 427px;
    padding: 24px;
    font-family: $source;
    background-color: $white;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    &__title {
      font-weight: 700;
      font-size: 1.125em; // 18px
      line-height: 1.333333333333333em; // 24px
      max-width: 379px;
      text-align: left;
      margin-bottom: 16px;
    }
    &__text {
      font-weight: 500;
      font-size: 1em; // 16px
      line-height: 1.5em; // 24px
      max-width: 379px;
      text-align: left;
      margin-bottom: 24px;
    }
    &__bounty {
      padding: 4px 8px;
    }
    &__date {
      font-weight: 600;
      font-size: 1em; // 16px
      line-height: 1.5em; // 24px
      float: right;
    }
  }
  @media screen and (max-width: 768px) {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.dotes {
  width: 100%;
  height: 16px;
  border: 4px dotted $light_gold;
  border-right: 0;
  border-left: 0;
  &:not(:last-child) {
    margin-bottom: 8px;
  }
}
