.kickstart {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 144px;
  .block {
    max-width: 644px;
    .kickstart__circle {
      @extend %circle_figure;
      margin-bottom: 24px;
    }
    @media screen and (max-width: 768px) {
      text-align: center;
    }
  }
  .widget {
    @extend %widget;
    box-shadow: 16px 16px 0 $green;
    margin-right: 60px;
    &__title {
      display: inline-block;
      font-weight: 700;
      font-size: 2em; //32px
      line-height: 1.25em; //40px
      margin-bottom: 28px;
    }
    @media screen and (max-width: 768px) {
      margin-right: unset;
    }
  }
  @media screen and (max-width: 768px) {
    flex-direction: column;
    align-items: center;
  }
}
