* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

//colors

$bg_color: #f9f9f9;

$black: #303031;

$gray: #66645e;

$green: #71a894;

$light_gold: #f3f0ea;

$white: #fff;

$yellow: #f5c64f;

$text_color: #303031;

a {
  text-decoration: none;
  color: #000;
}

body {
  background-color: $bg_color;
  font-size: 16px;
  @media screen and (max-width: 768px) {
    font-size: 12px;
  }
}

.container {
  margin: 0 auto;
  height: 100%;
  max-width: 1440px;
  padding: 0 56px;
  position: relative;
  @media screen and (max-width: 1024px) {
    padding: 15px 15px;
  }
  @media screen and (max-width: 768px) {
    padding: 10px 10px;
  }
}

.header_flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

//titles

.block__title {
  font-family: "BelyDisplay";
  font-size: 3em; // 48px
  line-height: 1.333333333333333em; // 64px
  margin: 0;
  margin-bottom: 24px;
}

%illustration_title {
  font-family: "BelyDisplay";
  font-size: 3em; // 48px
  line-height: 1.333333333333333em; // 64px
  margin: 0;
}

//text

.block__text {
  font-family: "ProximaNova";
  font-weight: 400;
  font-size: 1.125em; // 18px
  line-height: 1.444444444444444em; // 26px
  letter-spacing: -0.02em;
  margin: 0 auto;
  color: $text_color;
}

%illustration_text {
  font-family: "ProximaNova";
  font-weight: 400;
  font-size: 1.125em; // 18px
  line-height: 1.444444444444444em; // 26px
  letter-spacing: -0.02em;
  margin: 0 auto;
  color: $text_color;
}

//buttons

%button {
  min-width: 219px;
  min-height: 46px;
  background-color: $green;
  font-family: "ProximaNova";
  font-weight: 700;
  font-size: 1.125em;
  color: $white;
  border-radius: 8px;
  padding: 20px 24px;
  border: 0;
  box-shadow: 3px 0px 0px $light_gold;
}

.bounty {
  color: $white;
  background-color: $green;
  border-radius: 40px;
  min-width: 82px;
  max-height: 32px;
  border: 0;
  font-size: 1em; // 16px
  line-height: 1.5em; // 24px
}

//circle

%circle_figure {
  width: 88px;
  height: 88px;
  border-radius: 50%;
  background-color: $light_gold;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

%circle_client {
  width: 72px;
  height: 72px;
  border-radius: 50%;
  background-color: $light_gold;
  position: relative;
  overflow: hidden;
}

%circle_small_client {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: $white;
  position: relative;
  overflow: hidden;
}

//means_cards

%means_card_title {
  font-family: $proxima;
  font-weight: 700;
  font-size: 1.5em; // 24px
  line-height: 1.333333333333333em; // 32px
  text-align: center;
}

%means_card_text {
  font-family: $proxima;
  font-weight: 400;
  font-size: 1.125em; // 18px
  line-height: 1.444444444444444em; // 26px
  text-align: center;
}

//widgets

%widget {
  min-width: 484px;
  padding: 32px 40px;
  border: 4px solid #303031;
  border-radius: 16px;
  font-family: $source;
  letter-spacing: -0.02em;
  background-color: $white;
  .client {
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    &__circle {
      @extend %circle_client;
      margin-right: 16px;
    }
    .name_and_postname {
      display: flex;
      flex-direction: column;
      .name {
        font-weight: 600;
        font-size: 1.5em; // 24px
        line-height: 1.083333333333333em; // 26px
      }
      .postname {
        font-weight: 500;
        text-transform: uppercase;
        color: $gray;
        font-size: 1em;
        line-height: 1.625em;
        color: $gray;
      }
    }
    @media screen and (max-width: 1024px) {
      min-width: 350px;
      margin: 50px 20px 0 0;
    }
    @media screen and (max-width: 768px) {
      margin-top: 20px;
      min-width: 380px;
    }
    @media screen and (max-width: 425px) {
      margin-top: 20px;
      min-width: 250px;
    }
  }
  @media screen and (max-width: 768px) {
    padding: 5px 7px;
    margin-top: 30px;
    min-width: 200px;
  }
}
